oking-card .informations-container .more-information .disclaimer{margin-top:20px;color:white;font-size:0.8em}ul .booking-card:hover::before{background:rgba(10,72,112,0.6)}ul .booking-card:hover .book-container .content{opacity:1;transform:translateY(0px)}ul .booking-card:hover .informations-container{transform:translateY(0px)}ul .booking-card:hover .informations-container .more-information{opacity:1}@media (max-width: 768px){ul .booking-card::before{background:rgba(10,72,112,0.6)}ul .booking-card .book-container .content{opacity:1;transform:translateY(0px)}ul .booking-card .informations-container{transform:translateY(0px)}ul .booking-card .informations-container .more-information{opacity:1}}h1{margin:10px 20px}.alink{color:white}.project{color:white}.b1{background-image:url(/Portfolio/static/media/P1.82d1d8e9.jpg)}.b7{background-image:url(/Portfolio/static/media/P7.01ec9dda.png)}.b2{background-image:url(/Portfolio/static/media/P2.eec653c8.jpg)}.b3{background-image:url(/Portfolio/static/media/P3.ef573aab.jpg)}.b4{background-image:url(/Portfolio/static/media/P4.a1627cf4.jpg)}.b5{background-image:url(/Portfolio/static/media/P5.eaa41330.jpg)}.b6{background-image:url(/Portfolio/static/media/P6.ca123c60.png)}.projbox{padding-top:3%;padding-left:5%}.b8{background-image:url(/Portfolio/static/media/P8.bddafadd.jpg)}

#about {
	padding: 200px 0px 200px 0px;
	font-size: 50px;
	font-family: 'Fira Coda', monospace;
}


.aboutimage {
	height: 150px;
	width: 150px;
}

.boxing {
	padding-left: 12px;
}

.lead {
	padding: 5%;
	font-size: 1.25rem;
	font-weight: 300;
	font-family: 'Fira Coda', monospace;
}

.fonts {
	color: white;
}

.skillheading {
	padding-bottom: 30px;
}

#skills img {
	height: 5rem;
	width: 5rem;
	padding: 10px;
	padding-top: 10px;
	padding-right: 10px;
	padding-bottom: 10px;
	padding-left: 10px;
	border-radius: 5px;
}

.image {
	width: 80px;
	height: 80px;
	vertical-align: middle;
	border-style: none;
	border-top-style: none;
	border-right-style: none;
	border-bottom-style: none;
	border-left-style: none;
}

.skill {
	text-align: center;
	padding: 5px;
	padding-top: 5px;
	padding-right: 5px;
	padding-bottom: 5px;
	padding-left: 5px;
	border-radius: 5px;
	border-top-left-radius: 5px;
	border-top-right-radius: 5px;
	border-bottom-right-radius: 5px;
	border-bottom-left-radius: 5px;
	box-shadow: 0.2rem 0.25rem 0.75rem 0 rgba(0, 0, 0, 0.1);
	margin: 0.5rem;
	margin-right: 0.5rem;
	margin-bottom: 0.5rem;
	margin-left: 0.5rem;
	margin-top: 30px;
	cursor: pointer;
	transition: 0.4s;
}

.skill:hover {
	transform: scale(1.1, 1.1);
	box-shadow: 0.5rem 1.25rem 1.75rem 0 rgba(0, 0, 0, 0.3);
}

.skill p {
	font-family: 'Fira Coda', monospace;
	font-size: 0.8rem;
	color: black;
	margin-top: 5px;
	margin-bottom: 5px;
}

.footer {
	border-top: 0.5px solid #a3a4a5;
	margin-top: 5%;
	display: flex;
	justify-content: space-evenly;
	background-color: #95d3ff;
}

.footer a {
	display: block;
	color: #ffffff;
	padding: 18px 16px;
	text-decoration: none;
	text-align: center;
}

.contactimage {
	width: 50px;
	height: 50px;
}

.contactimage:hover {
	color: rgb(95, 114, 90);
	border-bottom: 0.5px solid #a3a4a5;
}

.fa:hover {
	opacity: 0.7;
}

